import { Layout, Row, Col, Button } from "antd";
import Favorites from "./Favorites";
import Register from "./Register";
import Login from "./Login";
import React from "react";
import CustomSearch from "./CustomSearch";

const { Header } = Layout;

function PageHeader({
  customSearchOnSuccess,
  loggedIn,
  signoutOnClick,
  signinOnSuccess,
  favoriteItems,
}) {
  return (
    <Header style={{ backgroundColor: "green" }}>
      <Row justify="space-between">
        <Col id="Logo">
          <h1 style={{ fontWeight: "bold", color: "white" }}>TwitchTrendz</h1>
        </Col>
        {loggedIn && (
          <Col>
            <CustomSearch onSuccess={customSearchOnSuccess} />
          </Col>
        )}
        <Col id="buttons">
          <Row>
            <Col>{loggedIn && <Favorites favoriteItems={favoriteItems} />}</Col>
            <Col>
              {loggedIn && (
                <Button shape="round" onClick={signoutOnClick}>
                  Logout
                </Button>
              )}
              {!loggedIn && (
                <div style={{ marginRight: "5px" }}>
                  <Login onSuccess={signinOnSuccess} />
                  <Register />
                </div>
              )}
            </Col>
          </Row>
        </Col>
      </Row>
    </Header>
  );
}

export default PageHeader;
