import React, { useState, useEffect } from "react";
import { Layout, message, Menu } from "antd";
import { LikeOutlined, FireOutlined } from "@ant-design/icons";
import {
  logout,
  getFavoriteItem,
  getTopGames,
  searchGameById,
  getRecommendations,
} from "./utils";
import PageHeader from "./components/PageHeader";
import Home from "./components/Home";
import Hero from "./components/Hero";

const { Content, Sider } = Layout;

function App() {
  const [loggedIn, setLoggedIn] = useState(false);
  const [favoriteItems, setFavoriteItems] = useState([]);
  const [topGames, setTopGames] = useState([]);
  const [resources, setResources] = useState({
    videos: [],
    streams: [],
    clips: [],
  });

  const [heroShow, setHeroShow] = useState(true);
  const checkLogin = async () => {
    try {
      const response = await fetch("/checklogin");

      if (response.ok) {
        setLoggedIn(true);
      } else if (response.status === 401) {
        // Handle 401 Unauthorized response without logging an error
        setLoggedIn(false);
        localStorage.removeItem("login");
      } else {
        // Handle other types of errors
        console.error("Error fetching login status:", response.status);
      }
    } catch (error) {
      // Handle network errors
      console.error("Network error:", error);
    }
  };
  useEffect(() => {
    checkLogin();

    getTopGames()
      .then((data) => {
        setTopGames(data);
      })
      .catch((err) => {
        message.error(err.message);
      });

    let loginState = localStorage.getItem("login");
    if (loginState === "true") {
      setLoggedIn(true);

      getFavoriteItem()
        .then((data) => {
          setFavoriteItems(data);
        })
        .catch((err) => {
          console.error(err.message);
        });
    }
  }, []);

  const signinOnSuccess = () => {
    setLoggedIn(true);
    try {
      getFavoriteItem().then((data) => {
        setFavoriteItems(data);
      });
    } catch (error) {
      console.log(error);
    }

    localStorage.setItem("login", "true");
  };

  const signoutOnClick = () => {
    logout()
      .then(() => {
        setLoggedIn(false);
        message.success("Successfully Signed out");
        localStorage.removeItem("login");
      })
      .catch((err) => {
        message.error(err.message);
      });
  };

  const customSearchOnSuccess = (data) => {
    setResources(data);
  };

  const onGameSelect = ({ key }) => {
    if (loggedIn) {
      setHeroShow(false);
    }
    if (!loggedIn) {
      message.info("You need to login for recommendations");
      return;
    }
    if (key === "recommendation") {
      getRecommendations().then((data) => {
        setResources(data);
        console.log(data);
      });

      return;
    }

    searchGameById(key).then((data) => {
      setResources(data);
      console.log(data);
    });
  };

  const favoriteOnChange = () => {
    getFavoriteItem()
      .then((data) => {
        setFavoriteItems(data);
      })
      .catch((err) => {
        message.error(err.message);
      });
  };

  const mapTopGamesToProps = (topGames) => [
    {
      label: "Recommend for you!",
      key: "recommendation",
      icon: <LikeOutlined />,
    },
    {
      label: "Popular Games",
      key: "popular_games",
      icon: <FireOutlined />,
      children: topGames.map((game) => ({
        label: game.name,
        key: game.id,
        icon: (
          <img
            alt="placeholder"
            src={game.box_art_url
              .replace("{height}", "40")
              .replace("{width}", "40")}
            style={{ borderRadius: "50%", marginRight: "20px" }}
          />
        ),
      })),
    },
  ];

  return (
    <Layout>
      <PageHeader
        loggedIn={loggedIn}
        signoutOnClick={signoutOnClick}
        signinOnSuccess={signinOnSuccess}
        favoriteItems={favoriteItems}
        customSearchOnSuccess={customSearchOnSuccess}
      />

      <Layout>
        <Sider
          theme="light"
          style={{ backgroundColor: "white" }}
          breakpoint="lg"
          collapsible={true}
          width={300}
          className="site-layout-background"
        >
          <Menu
            defaultOpenKeys={["popular_games"]}
            mode="inline"
            onSelect={onGameSelect}
            style={{ marginTop: "10px" }}
            items={mapTopGamesToProps(topGames)}
          />
        </Sider>
        <Layout style={{ padding: "24px" }}>
          <Content
            className="site-layout-background"
            style={{
              padding: 24,
              margin: 0,
              height: 800,
              overflow: "auto",
            }}
          >
            {heroShow && <Hero />}
            {loggedIn ? (
              <Home
                resources={resources}
                loggedIn={loggedIn}
                favoriteOnChange={favoriteOnChange}
                favoriteItems={favoriteItems}
              />
            ) : (
              <></>
            )}
          </Content>
        </Layout>
      </Layout>
    </Layout>
  );
}

export default App;
